<template>
  <section class="play-page">
    <div
      v-if="currentSong.song || currentSong.ar"
      class="mask"
      :style="{
        backgroundImage: `url(${
          currentSong.song ? currentSong.picUrl : currentSong.al.picUrl
        }?imageView=1&type=webp&thumbnail=246x0)`,
      }"
    ></div>

    <div
      v-else
      class="mask"
      :style="{
        backgroundImage: 'url(' + songPic + ')',
      }"
    ></div>

    <!-- 返回上一层 -->
    <div class="setDown" @click="$emit('toggle-show-play-page', false)"></div>

    <!--  歌手 歌名   -->
    <!-- <section class="songTitle" v-if="currentSong.album">
      <span class="song">{{ currentSong.name }}</span
      >-<span class="singer">{{ currentSong.song.artists[0].name }}</span>
    </section> -->

    <!-- 除了搜索区以外 -->
    <section class="songTitle" v-if="currentSong.song || currentSong.ar">
      <span class="song">{{ currentSong.name }}</span
      >-
      <span class="singer">{{
        currentSong.song
          ? currentSong.song.artists[0].name
          : currentSong.ar[0].name
      }}</span>
    </section>

    <!-- 搜索区-歌手 -->
    <section class="songTitle" v-else>
      <span class="song">{{ currentSong.name }}</span
      >-<span class="singer">{{ currentSong.artists[0].name }}</span>
      <!-- -<span class="singer">{{ currentSong.artists[0].name }}</span> -->
    </section>

    <!--  唱盘  -->
    <section class="rotate">
      <div
        class="barBox"
        @click="(showLyrics = true), (showRotate = false)"
        v-if="showRotate"
      >
        <img
          class="needle"
          :class="{ paused: !playing }"
          src="https://s3.music.126.net/mobile-new/img/needle-ab.png"
          alt=""
        />
        <section class="record" :class="{ playing: playing }">
          <img
            v-if="currentSong.song || currentSong.ar"
            class="thumb"
            :src="currentSong.song ? currentSong.picUrl : currentSong.al.picUrl"
            alt=""
          />
          <img v-else class="thumb" :src="songPic" alt="" />
          <img
            class="disc"
            src="https://s3.music.126.net/mobile-new/img/disc.png"
            alt=""
          />
        </section>
      </div>
    </section>

    <!-- 歌词 -->
    <transition
      enter-active-class="animate__animated animate__fadeIn"
      leave-active-class="animate__animated animate__fadeOut"
    >
      <section v-if="showLyrics">
        <section
          class="lyricsBox"
          @click="(showLyrics = false), (showRotate = true)"
        >
          <ul
            class="content"
            ref="lyricContent"
            v-if="parsedLyric.length"
            :style="{ marginTop: -scrollH + 'px' }"
          >
            <li v-for="(l, i) in parsedLyric" :key="i">
              <span
                :style="{
                  animationDuration: parsedLyric[i + 1]
                    ? parsedLyric[i + 1].time - l.time - 0.5 + 's'
                    : '3s',
                }"
                :class="{
                  active: currentLyricIndex === i,
                  playing: playing && currentLyricIndex === i,
                }"
                >{{ l.text }}</span
              >
            </li>
          </ul>
        </section>
      </section>
    </transition>

    <section class="progressBox">
      <div class="timeFirst">{{ currentTime | playTime }}</div>
      <!--  进度条  -->
      <section class="progress">
        <input
          type="range"
          :max="duration"
          step="0.5"
          v-model="value"
          @change="progressChange"
          @input="progressInput"
        />
        <span
          class="bar"
          :style="{ width: (value / duration) * 100 + '%' }"
        ></span>
        <!-- <input type="range" :max="duration" step="0.5" v-model="value" /> -->
      </section>
      <div class="timeLast">{{ duration | playTime }}</div>
    </section>

    <!--  控制区  -->
    <section class="controls">
      <div @click="$emit('toggle-play-model')" class="Sequence"></div>

      <div @click="$emit('prev-song')" class="Above"></div>

      <div
        @click="$emit('toggle-playing-state')"
        :class="[playing ? 'stop' : 'play']"
      ></div>

      <div @click="$emit('next-song')" class="Below"></div>

      <div
        @click.stop="$emit('toggle-show-play-list', true)"
        class="list"
      ></div>
    </section>
  </section>
</template>

<script>
import "animate.css";

export default {
  props: {
    currentSong: Object,
    playing: Boolean,
    duration: Number,
    currentTime: Number,
    songPic: String,
    item: Object,
  },
  data() {
    return {
      showLyrics: true,
      showRotate: false,

      value: this.currentTime,
      inputing: false,
      lyric: null,
      lisH: [],
      scrollH: 0,
    };
  },

  filters: {
    playTime: function (data) {
      let m = parseInt(data / 60);
      let s = parseInt(data % 60);
      if (m == 0 && s == 0) {
        return "0:00";
      } else {
        return m + ":" + (s < 10 ? "0" + s : s);
      }
    },
  },

  methods: {
    progressChange: function (event) {
      this.inputing = false;
      this.$emit("current-time-change", event.target.value);
    },
    progressInput: function () {
      this.inputing = true;
    },

    getLyric: function (id) {
      this.axios
        .get("/lyric", {
          params: { id },
        })
        .then((res) => {
          this.lyric = res.data.lrc.lyric;
          console.log(this.lyric);
        });
    },

    // //传入初始歌词文本text
    // formatLyric(text) {
    //   let arr = text.split("\n"); //原歌词文本已经换好行了方便很多，我们直接通过换行符“\n”进行切割
    //   let row = arr.length; //获取歌词行数
    //   for (let i = 0; i < row; i++) {
    //     let temp_row = arr[i]; //现在每一行格式大概就是这样"[00:04.302][02:10.00]hello world";
    //     let temp_arr = temp_row.split("]"); //我们可以通过“]”对时间和文本进行分离
    //     let text = temp_arr.pop(); //把歌词文本从数组中剔除出来，获取到歌词文本了！
    //     //再对剩下的歌词时间进行处理
    //     temp_arr.forEach((element) => {
    //       let obj = {};
    //       let time_arr = element.substr(1, element.length - 1).split(":"); //先把多余的“[”去掉，再分离出分、秒
    //       let s = parseInt(time_arr[0]) * 60 + Math.ceil(time_arr[1]); //把时间转换成与currentTime相同的类型，方便待会实现滚动效果
    //       obj.time = s;
    //       obj.text = text;
    //       this.lyric.push(obj); //每一行歌词对象存到组件的lyric歌词属性里
    //     });
    //   }
    // },
  },

  computed: {
    parsedLyric: function () {
      if (this.lyric) {
        return this.lyric
          .split("\n")
          .filter((s) => s)
          .map((s) => {
            var text = s.replace(/^\[\d{2}:\d{2}\.\d{2,3}\]/i, "");
            var timeStr = s.replace(text, "").replace(/(^\[|\]$)/g, "");
            var timeArr = timeStr.split(":").map((item) => Number(item));
            var time = timeArr[0] * 60 + timeArr[1];
            return { text, time };
          });
      } else {
        return [];
      }
    },
    currentLyricIndex: function () {
      var i = this.parsedLyric.findIndex(
        (item) => item.time > this.currentTime
      );
      var currentLyricIndex = i !== -1 ? i - 1 : this.parsedLyric.length - 1;
      return currentLyricIndex;
    },
  },

  watch: {
    currentTime: function (n) {
      if (!this.inputing) {
        this.value = n;
      }
    },

    "currentSong.id": function (id) {
      this.getLyric(id);
    },

    currentLyricIndex: function (index) {
      var h = this.lisH.slice(0, index).reduce(function (total, item) {
        return total + item;
      }, 0);
      // console.log(h);
      h = h > 200 ? h - 200 : 0;
      // h = h > 1928 ? 1928 : h;
      this.scrollH = h;
    },
    parsedLyric: function () {
      // console.log("parsedLyric改变", this.$refs.lyricContent);
      this.$nextTick(() => {
        // console.log("nextTick", this.$refs.lyricContent);
        var lis = this.$refs.lyricContent.querySelectorAll("li");
        this.lisH = [...lis].map((item) => item.offsetHeight);
      });
    },
  },

  created() {
    console.log(this.currentSong.id);
    this.getLyric(this.currentSong.id);
  },
};
</script>

<style lang="less" scoped>
* {
  margin: 0;
  padding: 0;
}
.animate__animated {
  animation-duration: 1s;
}

@keyframes xxx {
  from {
    background-position-x: 0;
  }
  to {
    background-position-x: -100%;
  }
}
.active {
  /* cursor: pointer; */
  animation-name: xxx;
  animation-timing-function: linear;
  animation-duration: 3s;
  background-image: linear-gradient(to right, red 50%, blue 50%);
  background-size: 200%;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.pos-ab() {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.play-page {
  width: 100vw;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;

  // 歌手 歌名
  .songTitle {
    width: 100%;
    height: 35px;
    position: relative;
    text-align: center;
    margin: 0 auto;
    font: 14px/1.5 Helvetica, sans-serif, STHeiTi;
    line-height: 1.6em;
    box-shadow: 0px -2px 0px 0px rgb(242, 239, 255) inset;
    // background-image: linear-gradient(
    //   to right,
    //   rgb(14, 196, 252) 50%,
    //   rgb(28, 96, 255) 50%
    // );
    // background-size: 200%;
    // -webkit-background-clip: text;
    // -webkit-text-fill-color: transparent;
    // text-shadow: -1px -1px 1px rgba(22, 139, 248, 0.733);
    color: #f8f1f0;
    position: absolute;
    top: 20px;
    .song {
      font-size: 19px;
      margin-right: 15px;
    }
    .singer {
      opacity: 0.5;
      margin-left: 15px;
    }
  }

  .setDown {
    background-image: url(../assets/setdown.png);
    z-index: 10;
    width: 25px;
    height: 25px;
    margin-top: 18px;
    margin-left: 15px;
    background-size: cover;
    position: relative;
    top: 0;
  }

  overflow: hidden;
  &::before {
    content: "";
    display: block;
    .pos-ab();
    z-index: -2;
    background-color: #333;
  }

  .mask {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    filter: blur(30px) brightness(0.8);
    .pos-ab();
    z-index: -1;
    transform: scale(1.5);
    transition: all 0.3s;
  }

  // 唱盘
  .rotate {
    margin-top: 15px;
    position: relative;
    padding-top: 23vw;
    height: 450px;
    .barBox {
      img.needle {
        height: 40vw;
        position: absolute;
        top: 0;
        left: 50%;
        z-index: 9;
        margin-left: -15px;
        transform-origin: 15px 15px;
        transform: rotate(0deg);
        transition: all 0.5s;
        &.paused {
          transform: rotate(-30deg);
        }
      }
      .record {
        position: relative;
        width: 80vw;
        height: 80vw;
        margin: 0 auto 0 auto;
        img {
          .pos-ab();
          border-radius: 50%;
          &.thumb {
            transform: scale(0.8);
          }
        }
        animation: rotate 25s linear infinite;
        animation-play-state: paused;
        &.playing {
          animation-play-state: running;
        }
      }
    }
  }

  // 歌词
  .lyricsBox {
    position: fixed;
    top: 60px;
    left: 0;
    width: 100vw;
    height: 450px;
    // background-color: red;
    // opacity: 0.8;
    z-index: 13;
    overflow: scroll;
    text-align: center;
    font-size: 14px;
    ul {
      transition: all 0.8s;
      color: white;
      li {
        line-height: 1.8;
        line-height: 3;
        span {
          &.active {
            animation-name: xxx;
            animation-timing-function: linear;
            animation-duration: 3s;
            background-image: linear-gradient(
              to right,
              rgb(238, 20, 111) 50%,
              rgb(28, 96, 255) 50%
            );
            background-size: 200%;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            text-shadow: 0 0 1px rgb(194, 76, 248);

            animation-play-state: paused;
            &.playing {
              animation-play-state: running;
            }
          }
        }
      }
    }
  }

  .progressBox {
    .timeFirst {
      color: #ebeee3;
      font: 14px/1.5 Helvetica, sans-serif, STHeiTi;
      margin-left: 10px;
    }
    .timeLast {
      color: #ebeee3;
      opacity: 0.5;
      font: 14px/1.5 Helvetica, sans-serif, STHeiTi;
      margin-right: 10px;
    }
    // 进度条
    display: flex;
    align-items: center;

    .progress {
      flex-basis: 75%;
      height: 50px;
      background-color: skyblue;
      height: 4px;
      margin: 20px auto;
      background: #78554e;
      position: relative;
      border-radius: 10px;
      width: 75%;

      input {
        width: 100%;
        height: 100%;
        opacity: 0;
        position: absolute;
        z-index: 1;
        top: 20px;
      }
      .bar {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: #e99382;
        border-radius: inherit;
        &::before {
          content: "";
          width: 10px;
          height: 10px;
          display: block;
          margin-right: -5px;
          background: #fefcfd;
          position: absolute;
          top: -3px;
          right: 0;
          border-radius: 50%;
        }
      }
    }
  }

  // 控制区
  .controls {
    // margin-top:130px;
    padding-left: 10%;
    padding-right: 10%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    color: white;
    .Sequence {
      width: 20px;
      height: 20px;
      background-image: url(../assets/Sequence.png);
      background-size: cover;
    }

    .Above {
      width: 20px;
      height: 20px;
      background-image: url(../assets/Above.png);
      background-size: cover;
    }
    .play {
      width: 45px;
      height: 45px;
      background-image: url(../assets/play.svg);
      background-size: cover;
    }
    .stop {
      width: 45px;
      height: 45px;
      background-image: url(../assets/stop.png);
      background-size: cover;
    }
    .Below {
      width: 20px;
      height: 20px;
      background-image: url(../assets/Below.png);
      background-size: cover;
    }
    .list {
      width: 20px;
      height: 20px;
      background-image: url(../assets/list.png);
      background-size: cover;
    }
  }
}
</style>
