<template>
  <footer class="play" :class="{ playing: playing }">
    <transition
      name="custom-classes-transition"
      enter-active-class="animate__animated animate__fadeIn"
      leave-active-class="animate__animated animate__fadeOut"
    >
      <keep-alive>
        <PlayPage
          v-if="showPlayPage"
          @toggle-show-play-page="showPlayPage = $event"
          @toggle-show-play-list="showPlayList = $event"
          :currentSong="currentSong"
          :currentTime="currentTime"
          :duration="duration"
          :playing="playing"
          @toggle-playing-state="$emit('toggle-playing-state')"
          @next-song="$emit('next-song')"
          @prev-song="$emit('prev-song')"
          @toggle-play-model="$emit('toggle-play-model')"
          @current-time-change="$emit('current-time-change', $event)"
          :songPic="songPic"
          :lyric="lyric"
        />
      </keep-alive>
    </transition>
    <transition
      name="custom-classes-transition"
      enter-active-class="animate__animated animate__slideInUp"
      leave-active-class="animate__animated animate__slideOutDown"
    >
      <PlayBar
        v-show="!showPlayPage"
        :currentPlayList="currentPlayList"
        :currentSong="currentSong"
        :currentTime="currentTime"
        :duration="duration"
        :playing="playing"
        @toggle-playing-state="$emit('toggle-playing-state')"
        @toggle-show-play-list="showPlayList = $event"
        @toggle-show-play-page="showPlayPage = $event"
        :songPic="songPic"
        :lyric="lyric"
      />
    </transition>
    <PlayList
      v-if="showPlayList"
      :currentPlayList="currentPlayList"
      :currentSong="currentSong"
      :playing="playing"
      @toggle-show-play-list="showPlayList = $event"
      @change-current-song="$emit('change-current-song', $event)"
    />
  </footer>
</template>

<script>
import "animate.css";
import PlayBar from "../components/PlayBar.vue";
import PlayPage from "../components/PlayPage.vue";
import PlayList from "../components/PlayList.vue";
export default {
  components: {
    PlayBar,
    PlayPage,
    PlayList,
  },
  props: {
    changeCurrentSong: Object,
    currentSong: Object,
    playing: Boolean,
    duration: Number,
    currentTime: Number,
    currentPlayList: Array,
    lyric: [],
  },
  data: function () {
    return {
      showPlayList: false,
      showPlayPage: false,
      songPic: "",
    };
  },

  created: function () {
    this.getSongPic(this.currentSong.id);
  },
  methods: {
    getSongPic: function (id) {
      this.axios
        .get("/song/detail", {
          params: {
            ids: id,
          },
        })
        .then((data) => {
          // console.log(data);
          this.songPic = data.data.songs[0].al.picUrl;
          // console.log(this.songPic);
        });
    },
  },

  watch: {
    "currentSong.id": function (id) {
      // console.log(id);
      this.getSongPic(id);
    },
  },
};
</script>

<style lang="less"></style>
