<template>
  <li 
  @click="$router.push('/playlist?id=' + item.id),show = false" 
  class="card-item"
  @showTop="showTop"
  >

    <div class="thumb">
      <img
        class="bg"
        :src="`${item.picUrl}?imageView=1&type=webp&thumbnail=246x0`"
      />
      <span
        ><img
          style="width: 11px;height:10px"
          src="data:image/svg+xml;base64,"
          alt=""
        />
        {{ item.playCount | parseCount }}</span
      >
    </div>
    <p>{{ item.name }}</p>
  </li>
</template>

<script>
export default {
  props: {
    item: {
      type: Object,
      required: true,
      show:Boolean,
    },
  },

  methods: {
    showTop(show){
      return console.log(show)
    }
  },

  filters: {
    parseCount: function(value) {
      // return value + "0";
      if (value > 100000000) {
        return (value / 100000000).toFixed(1) + "亿";
      } else if (value > 10000) {
        return (value / 10000).toFixed(1) + "万";
      } else {
        return value;
      }
    },
  },
};
</script>

<style lang="less">
.card-item {
  flex-basis: 25%;
  flex-grow: 1;



  &:nth-child(3n + 2) {
    margin: 0 3px;
  }

  .thumb {
    position: relative;
    top: 0;
    left: 0;
    // background: red;
    font-family: Helvetica,sans-serif,STHeiTi;
    img.bg {
      vertical-align: middle;
    }
    span {
      position: absolute;
      top: 2px;
      right: 4px;
      font-size: 12px;
      color: #fff;
    }
  }
  p {
    font-size: 13px;
    margin: 5px;
    margin-bottom: 10px;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
    text-overflow: ellipsis;
    
  }
}
</style>
