<template>
  <div v-if="detail">
    <!-- <h3>PlayList {{ $route.query.id }}</h3> -->
    <div class="black" @click.stop="back()"></div>
    <section class="plhead">
      <div
        class="bg"
        :style="{
          backgroundImage: `url(${detail.coverImgUrl}?param=170y170)`,
        }"
      ></div>
      <div class="wrap">
        <div class="fl">
          <img
            class="u-img"
            :src="`${detail.coverImgUrl}?imageView=1&type=webp&thumbnail=378x0`"
          />
          <span class="icon">歌单</span
          ><span class="erji"
            ><img
              style="width: 12px"
              src="data:image/svg+xml;base64,"
            />
            {{ detail.playCount | parseCount }}</span
          >
        </div>
        <div class="fr">
          <h2 class="title">
            {{ detail.name }}
          </h2>
          <div class="auth">
            <img
              class="u-img"
              :src="`${detail.creator.avatarUrl}?imageView=1&amp;type=webp&amp;thumbnail=90x0`"
            />
            <span>{{ detail.creator.nickname }}</span>
          </div>
        </div>
      </div>
    </section>
    <div class="titleList">歌曲列表</div>

    <ul>
      <!-- <li v-for="item in detail.tracks" :key="item.id">{{ item.name }}</li> -->
      <SongListItem
        v-for="(item, index) in detail.tracks"
        :key="item.id"
        :item="item"
        @change-current-song="
          $emit('change-current-song', item);
          $emit('change-current-play-list', detail.tracks);
        "
        :currentSongId="currentSongId"
        :playing="playing"
        :class="{ lt3: index < 3 }"
        >{{ index + 1 }}</SongListItem
      >
    </ul>
  </div>
</template>

<script>
import SongListItem from "@/components/SongListItem.vue";
export default {
  components: {
    SongListItem,
  },
  props: {
    currentSongId: {
      type: Number,
    },
    playing: Boolean,
    changeCurrentSong: Object,
  },
  data: function () {
    return {
      detail: null,
    };
  },
  methods: {
    back() {
      this.$router.go(-1); //路由负1
      console.log("返回");
    },
    getPlayListDetail: function (id) {
      this.axios
        .get("/playlist/detail", {
          params: {
            id,
          },
        })
        .then((res) => {
          console.log(res);
          this.detail = res.data.playlist;
        });
    },
  },

  filters: {
    parseCount: function (value) {
      // return value + "0";
      if (value > 100000000) {
        return (value / 100000000).toFixed(1) + "亿";
      } else if (value > 10000) {
        return (value / 10000).toFixed(1) + "万";
      } else {
        return value;
      }
    },
  },

  watch: {
    "currentSong.id": function (id) {
      this.getPlayListDetail(id);
    },
  },

  created: function () {
    this.getPlayListDetail(this.$route.query.id);
  },
};
</script>

<style lang="less" scoped>
.black{
  background-image: url(../assets/black.png);
  background-size: cover;
  width: 22px;
  height: 22px;
  position: absolute;
  left: 5px;
  top: 2px;
}
.plhead {
  width: 100vw;
  height: 186px;
  position: relative;
  padding: 30px 10px 30px 15px;
  overflow: hidden;
  z-index: -9;
  .bg {
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50%;
    filter: blur(5px);
    transform: scale(1.5);
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;

    &::after {
      content: "";
      display: block;
      background-color: rgba(0, 0, 0, 0.25);
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
    }
  }
  .wrap {
    display: flex;
    position: relative;
    z-index: 2;
    .fl {
      width: 126px;
      height: 126px;
      position: relative;
      .u-img {
        width: 100%;
        vertical-align: middle;
      }
      .icon {
        position: absolute;
        z-index: 3;
        top: 10px;
        left: 0;
        padding: 0 8px;
        height: 17px;
        color: #fff;
        font-size: 9px;
        text-align: center;
        line-height: 17px;
        background-color: rgba(217, 48, 48, 0.8);
        border-top-right-radius: 17px;
        border-bottom-right-radius: 17px;
      }
      .erji {
        position: absolute;
        right: 2px;
        top: 0;
        z-index: 3;
        padding-left: 15px;
        color: #fff;
        font-size: 12px;
        background-position: 0;
        background-repeat: no-repeat;
        background-size: 11px 10px;
        text-shadow: 1px 0 0 rgb(0 0 0 / 15%);
      }
    }
    .fr {
      flex: 1 1 auto;
      width: 1%;
      margin-left: 16px;
      display: -webkit-box;
      -webkit-box-pack: center;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
      overflow: hidden;
      text-overflow: ellipsis;
      .title {
        padding-top: 1px;
        font-size: 17px;
        line-height: 1.3;
        color: #fefefe;
        height: 44px;
        margin-left:5px ;
      }
      .auth {
        display: inline-block;
        color: hsla(0, 0%, 100%, 0.7);
        font: 14px/1.5 Helvetica, sans-serif, STHeiTi;
        margin-top: 15px;

        .u-img {
          width: 30px;
          height: 30px;
          vertical-align: middle;
          border-radius: 50%;
          margin-right: 5px;
        }
      }
    }
  }
}
.titleList {
  height: 23px;
  line-height: 23px;
  padding: 0 10px;
  font-size: 12px;
  color: #666;
  background-color: #eeeff0;
}
</style>