<template>
  <div class="search" @scroll="scroll">
    <div class="input_box">
      <div class="input_content">
        <div class="searchImg"></div>
        <input
          type="text"
          placeholder="搜索歌曲、歌手、专辑"
          v-model.trim="value"
          @keyup.enter="value && (searching = true)"
          @focus="inputing = true"
          @blur="inputing = false"
        />
      </div>
    </div>

    <!--   热门搜索列表   -->
    <section class="hots" v-if="!value && !searching">
      <div class="searchHotBox">
        <div class="searchHot">热门搜索</div>
        <ul>
          <li
            v-for="(hot, index) in hots"
            :key="index"
            @click="
              searching = true;
              value = hot.first;
            "
          >
            {{ hot.first }}
          </li>
        </ul>
      </div>

      <!--   历史搜索记录   -->
      <div class="searchHistory" ref="searchHistory">
        <div class="clear" @click="clear">清除</div>
        <ol>
          <li
            v-for="(h, index) in history"
            :key="index"
            @click.stop="
              searching = true;
              value = h;
            "
          >
            <div class="historyImg"></div>
            <span>{{ h }}</span>
            <div class="removeImg" @click.stop="remove(h)" ref="removeImg"></div>
          </li>
        </ol>
      </div>
    </section>

    <!--   搜索建议列表   -->
    <section class="suggests" v-if="value && !searching">
      <div class="searchTitle">搜索“{{ value }}”</div>
      <ul>
        <li
          v-for="(item, index) in suggests"
          :key="index"
          @click="
            searching = true;
            value = item.keyword;
          "
        >
          <div class="searchImg"></div>
          <span>{{ item.keyword }}</span>
        </li>
      </ul>
    </section>

    <!--  搜索结果   -->
    <section class="result" v-if="searching">
      <div class="resultTitle">最佳匹配</div>
      <!-- <ul>
        <li v-for="(item, index) in searchResults" :key="index">
          {{ item.name }}
        </li>
      </ul> -->

      <SongListItem
        v-for="(item, index) in searchResults"
        :key="index"
        :item="item"
        @change-current-song="
          $emit('change-current-song', $event);
          $emit('change-current-play-list', searchResults);
        "
        :currentSongId="currentSongId"
        :playing="playing"
      ></SongListItem>
      <p v-if="!hasMore">没有更多了</p>
    </section>
  </div>
</template>

<script>
import SongListItem from "../components/SongListItem.vue";
export default {
  name: "Search",
  components: { SongListItem },
  props: {
    currentSongId: {
      type: Number,
    },
    item:Object,
    playing: Boolean,
  },
  data: function () {
    return {
      hots: [],
      suggests: [],
      searchResults: [],
      value: "",
      searching: false,
      inputing: false,
      page: 0,
      hasMore: false,
      history: JSON.parse(window.localStorage.getItem("history")) || [],
      historyOld: this.history,
    };
  },
  methods: {
    scroll: function (event) {
      //   console.log(event);
      // document.querySelector(".search").scrollHeight;
      // document.querySelector(".search").scrollTop;
      // document.querySelector(".search").offsetHeight;
      if (this.hasMore) {
        if (
          event.target.offsetHeight + event.target.scrollTop + 1 >
          event.target.scrollHeight
        ) {
          console.log("触底");

          this.getSearch();
        }
      } else {
        console.log("没有更多了");
      }
    },
    getSearch: function () {
      this.axios
        .get("/search", {
          params: {
            keywords: this.value,
            limit: 30,
            offset: this.page * 30,
          },
        })
        .then((res) => {
          this.searchResults.push(...res.data.result.songs);
          this.page++;
          this.hasMore = res.data.result.hasMore;
        });

      // 记录一下
      // this.history.push(this.value)
      this.history = [...new Set([...this.history, this.value])];
      window.localStorage.setItem("history", JSON.stringify(this.history));
    },

    remove(value) {
      console.log(history);
      this.history = this.history.filter((item) => item !== value);
      window.localStorage.setItem("history", JSON.stringify(this.history));
    },
    clear(e) {
      console.log(history);
      this.history.splice(e);
      // this.history.splice(this.history[i].findIndex(e=>e.i === 1),1)
      // localStorage.setItem("history", this.value)
      window.localStorage.clear();
    },
  },

  created: function () {
    this.axios.get("/search/hot").then((res) => {
      this.hots = res.data.result.hots;
    });
  },

  watch: {
    value: function (n) {
      if (this.inputing) {
        this.searching = false;
      }
      if (n && !this.searching) {
        this.axios
          .get("/search/suggest", {
            params: {
              keywords: n,
              type: "mobile",
            },
          })
          .then((res) => {
            this.suggests = res.data.result.allMatch;
          });
      } else {
        this.suggests = [];
      }
    },

    searching: function (n) {
      if (n && this.value) {
        this.getSearch();
      } else {
        this.searchResults = [];
      }
    },
  },
};
</script>

<style lang="less" scoped>
* {
  margin: 0;
  padding: 0;
}
.search {
  font: 14px/1.5 Helvetica, sans-serif, STHeiTi;
  // 输出框
  .input_box {
    width: 360px;
    height: 60px;
    // background-color: skyblue;
    box-shadow: 0px -1px 0px 0px rgb(242, 239, 255) inset;
    padding: 15px 10px;
    // opacity: 0.9;
    .input_content {
      position: relative;
      background: #ebecec;
      border-radius: 30px;
      width: 340px;
      height: 30px;
      box-sizing: border-box;
      padding: 0 30px;
    }
    input {
      border: none;
      width: 280px;
      height: 30px;
      position: relative;
      outline: none;
      font-size: 14px;
      // line-height: 18px;
      // box-sizing: border-box;
      padding-top: 2px;

      color: #333;
      background: #ebecec;
      &::-webkit-input-placeholder {
        opacity: 0.3;
        // text-align: left;
      }
    }
    .searchImg {
      background-image: url(../assets/search.svg);
      width: 13px;
      height: 13px;
      position: absolute;
      left: 0;
      top: 9px;

      margin: 0 8px;
      vertical-align: middle;
    }
  }

  // 热门搜索列表
  .hots {
    .searchHotBox {
      width: 360px;
      height: 157px;
      padding: 15px 10px 0;
      font: 14px/1.5 Helvetica, sans-serif, STHeiTi;
      color: #333;

      .searchHot {
        font-size: 12px;
        line-height: 12px;
        color: #666;
      }

      ul {
        // overflow: hidden;
        li {
          display: inline-block;
          margin-top: 10px;
          height: 32px;
          margin-right: 8px;
          margin-bottom: 8px;
          padding: 0 14px;
          font-size: 14px;
          line-height: 32px;
          color: #333;

          box-shadow: 1px -1px 1px 1px rgb(242, 239, 255) inset;
        }
      }
    }
    .searchHistory {
      position: relative;
      left: 0;
      top: 30px;
      .clear {
        margin-left: 280px;
        display: inline-block;
        margin-top: 10px;
        height: 32px;
        margin-right: 8px;
        margin-bottom: 8px;
        padding: 0 14px;
        font-size: 14px;
        line-height: 32px;
        color: #333;
        box-shadow: 1px -1px 1px 1px #f2efff inset;
      }
      ol {
        li {
          display: flex;
          width: 360px;
          height: 45px;
          align-items: center;
          box-shadow: 0px -1px 1px 0px rgb(242, 239, 255) inset;
          .historyImg {
            background-image: url(../assets/history.svg);
            width: 15px;
            height: 15px;
            margin: 0 10px;
          }
          height: 45px;
          .removeImg {
            background-image: url(../assets/remove.svg);
            width: 12px;
            height: 12px;
          }
          span {
            flex-basis: 82%;
            width: 360px;
            height: 20px;
            font-size: 14px;
          }
        }
      }
    }
  }

  // 搜索建议列表
  .suggests {
    .searchTitle {
      height: 50px;
      margin-left: 10px;
      padding-right: 10px;
      font-size: 15px;
      line-height: 50px;
      color: #507daf;

      box-shadow: 0px -1px 0px 0px rgb(242, 239, 255) inset;
    }
    ul {
      font-size: 15px;
      padding-left: 10px;
      li {
        display: flex;
        align-items: center;
        line-height: 45px;
        padding-right: 10px;
        color: #333;
        .searchImg {
          width: 15px;
          height: 15px;
          background-image: url(../assets/search.svg);
          margin-right: 7px;
          background-size: cover;
        }
        span {
          width: 100%;
          box-shadow: 0px -1px 0px 0px rgb(242, 239, 255) inset;
        }
      }
    }
  }

  // 搜索结果
  .result {
    .resultTitle {
      margin-left: 10px;
      font-size: 12px;
      line-height: 16px;
      color: #666;
    }
  }
}
</style>
